<template>
	<view class="container">
		<view class="upload-wrapper" flex="main:justify cross:center">
			<view>
				<uni-file-picker
					disable-preview
					:del-icon="false"
					return-type="object"
					@select="select"
					:image-styles="imageStyles"
				>
				<u-image src="../../static/images/wallet/upload_plus.png" width="126upx" height="126upx" />
				</uni-file-picker>
				<view class="recharge-img">转账截图</view>
			</view>
			<view flex="main:center cross:center">
				<text style="color: #fff; margin-right: 20upx;">{{i18n.submitRecharge.exImg}}</text>
				<u-image src="../../static/images/wallet/ex_img.png" width="112upx" height="224upx" />
			</view>
		</view>
		<view class="title">
			<text>{{i18n.submitRecharge.hash}}</text>
			<u-image class="title-bg" src="../../static/images/wallet/title-long-bg.png" width="144upx" height="12upx" mode="" />
		</view>
		<view class="input-wrapper">
			<input type="text" v-model="transfer_addr" class="address-input" />
			<view class="copy-btn" @click="handlePaste">{{i18n.submitRecharge.copyBtn}}</view>
		</view>
		<view flex="main:justify cross:center" style="padding: 0 30upx;">
			<view class="submit-btn" @click="handleSubmit">{{i18n.submitRecharge.submitBtn}}</view>
			<view class="close-btn" @click="openPage">{{i18n.submitRecharge.closeBtn}}</view>
		</view>
		<view>
			<view class="note">{{i18n.submitRecharge.note}}</view>
			<c-tips v-for="(item,index) in rechargeInfo.note" :text="item" :key="index" />
		</view>
	</view>
</template>

<script>
import { mapState, mapActions } from 'vuex';
import { commonMixin } from '@/common/mixin/mixin.js';
// import UploadImages from '@/components/upload-images';
import UniFilePicker from '@/uni_modules/uni-file-picker/components/uni-file-picker/uni-file-picker'
export default {
	mixins: [commonMixin],
	components: { UniFilePicker },
	data() {
		return {
			tabIndex: 1,
			rechargeInfo: {},
			amount: null,
			transfer_img: '',
			transfer_addr: null,
			imageStyles: {
				width:64,
				height:64
			}
		};
	},
	computed: {
		...mapState('user', ['loginInfo'])
	},
	onLoad(e) {
		this.amount = e.amount
	},
	onShow() {
		uni.setNavigationBarTitle({
			title: this.i18n.submitRecharge.title
		});
		this.loadData()
	},
	methods: {
		...mapActions('user', ['getFinaceInfo', 'financeRecharge']),
		...mapActions('common', ['uploadImg']),
		//请求数据
		async loadData() {
			this.getFinaceInfo({config: 'recharge'}).then(res=> {
				this.rechargeInfo = res.data
			})
		},
		success(e) {
			console.log('e',e)
		},
		select(e) {
			console.log('e',e)
			let file = e.tempFiles[0].file
			let tempFilePaths = e.tempFilePaths
			this.uploadImg({img:file}).then(res=> {
				
			})
		},
		handleSubmit() {
			// console.log('img', this.transfer_img)
			let params = {
				coin_type: 'USDT-TRC20',
				amount: this.amount,
				transfer_img: '/static/images/b/BTC.png',
				transfer_addr: 'ddgg23fser134tgvrw'
			}
			this.financeRecharge(params).then(res=> {
				this.$api.msg('充值成功')
			})
		},
		// 粘贴
		handlePaste() {
			uni.getClipboardData({
				success: (res) => {
					this.transfer_addr = res.data
					console.log(res.data);
				}
			})
		},
		openPage() {
			uni.navigateBack()
		}
	},
	
};
</script>

<style lang="scss" scoped>
.upload-wrapper {
	padding: 100upx 130upx 50upx 153upx;
}
.title {
	width: 100%;
	height: 28upx;
	font-size: 30upx;
	font-family: PingFang SC;
	font-weight: 500;
	color: #ffffff;
	padding: 0 26upx;
	display: flex;
	flex-direction: column;
	text {
		margin-left: 6upx;
		z-index: 10;
	}
	.title-bg {
		margin-top: -30upx;
	}
}
.input-wrapper {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
	padding: 0 29upx;
	margin: 40upx 0;
	.address-input {
		width: 511upx;
		height: 76upx;
		border: 2upx solid #554472;
		border-radius: 4upx;
		color: #fff;
		padding: 0 22upx;
	}
	.copy-btn {
		width: 153upx;
		height: 76upx;
		line-height: 76upx;
		background: url(../../static/images/public/login-btn.png);
		background-size: 100% 100%;
		color: #fff;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 26upx;
	}
}
.submit-btn {
	width: 328upx;
	height: 76upx;
	line-height: 76upx;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 26upx;
	background: linear-gradient(90deg, #3fbbfe, #a541ff);
	border-radius: 4px;
}
.close-btn {
	width: 328upx;
	height: 76upx;
	line-height: 76upx;
	color: #fff;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 26upx;
	background: url(../../static/images/public/login-btn.png);
	background-size: 100% 100%;
	color: #fff;
	font-size: 26upx;
}
.note {
	font-size: 32upx;
	font-family: PingFang SC;
	font-weight: 500;
	color: #FFFFFF;
	margin: 30upx;
	margin-top: 130upx;
}
.recharge-img {
	font-size: 30upx;
	font-family: PingFang SC;
	font-weight: 500;
	color: #FFFFFF;
	margin-top: 16upx;
	margin-left: -10upx;
}
</style>
